html,
body {
  margin: 0;
  width: 100%;
  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal !important;
}

* {
  -webkit-font-smoothing: antialiased;
}

body.modal-open {
  overflow: hidden !important;
}

h1 {
  margin: 0;
  font-family: "DM Serif Text";
}

#root {
  width: 100%;
  height: 100%;
}

.emphasis {
  color: #11782e;
  border-bottom: 1px solid #11782e;
}

.green {
  color: #11782e;
}

.mobileOnly {
  display: none;
}

@media screen and (max-width: 767px) {
  .mobileOnly {
    display: initial;
  }
  .desktopOnly {
    display: none;
  }
}
